import { Component, TiledMap, _decorator } from "cc";

class Person {
  name: string;
  age: number;
  sex: string;
  relations: [{}];
  index: 0;

  constructor(name: string, age: number, sex: string) {
    this.name = name;
    this.age = age;
    this.sex = sex;
    this.relations = [{}];
    this.index = 0;
  }

  addRelation(people: Person, whatRalation: string) {
    this.relations.push({ r_ship: whatRalation, r_name: people.name });
  }
}
